<p>不限于文字、软件、声音、相片、录像、图表、动画、音效、插图及软件;广告中的全部内容;电子邮件中的全部内容;本平台为用户提供的其他信息。所有这些信息均受版权、商标、标签和其他财产所有权法律的保护。用户只能在取得本平台和广告商的授权下才能使用这些内容，不能擅自复制、再造或创造与这些内容有关的派生产品。本平台的所有内容版权归原文作者和本平台共同所有，任何人需要转载本平台的内容，必须获得本平台的书面授权。 
</p>
<p>5.5. 用户承诺自己在使用本平台时实施的所有行为均遵守国家法律、法规和本平台的相关规定以及各种社会公共利益或公共道德，如有违反而导致任何法律后果的发生，用户将以自己的名义独立承担所有相应的法律责任。</p>
<p> </p>
<p> </p>
<p>6. 争议解决方法</p>
<p> </p>
<p>凡因解释或执行服务协议所发生的或与本服务协议有关的一切争议，协议双方先通过友好协商解决;如协商不成，任何一方有权向本平台的公司注册地有管辖权的人民法院诉讼解决。</p>
<p> </p>
<p>7. 附则</p>
<p> </p>
<p>7.1用户点击“同意”按钮即视为用户签署本协议。本协议自用户签署之时起生效，有效期至用户申请注销或本平台主动注销用户名为止。</p>
<p>7.2我们尊重用户的合法权利，本用户协议及本站上发布的各类规则、政策、声明等其他内容，均是为了更好的、更加便利的为用户和消费者提供服务。本平台欢迎用户和社会各界提出意见和建议，平台将虚心接受并适时修改本服务条款及相关政策、规则。用户完成注册且成为平台的注册用户则意味着用户完全接受本用户协议，在注册之前请用户再次确认已知悉并完全理解本用户协议的全部内容。
</p>
