<p>（2）您通过本平台服务向第三方分享的信息，以及您使用本平台服务时所存储的信息。</p>
<p>请注意，如您在本平台服务中其他用户可见的公开区域内上传或发布的信息中、您对其他人上传或发布的信息作出的回应中公开您的信息，该等信息可能会被他人收集并加以使用。当您发现他人不正当地收集或使用您的信息时，可通过本政策"如何联系我们"章节所列的反馈渠道联系我们。
</p>
<p>2、第三方共享的您的信息。亦即第三方使用本平台服务时所提供有关您的共享信息。</p>
<p>3、我们获取的您的信息。您在使用本平台服务时，我们收集、汇总、记录的信息，包括日志信息、位置信息、设备信息。</p>
<p>（二）我们会出于以下目的，收集和使用您以下类型的个人信息。</p>
<p>您理解并同意，我们希望提供给您的产品和服务是完善的，所以我们会不断改进我们的产品和服务，这意味着随着产品和服务的迭代、拓展、升级等，我们可能会经常推出新的服务或业务功能或重新划分业务功能，并可能需要收集新的个人信息或变更个人信息使用目的或方式。请您注意，如果我们要将您的个人信息用于本政策中未载明的其他用途或额外收集未提及的其他个人信息，我们会通过页面提示、交互流程、网站公告或其他方式另行征得您的同意。一旦您同意，该等额外用途将视为本政策的一部分，该等额外信息也将适用本政策。在此过程中，如您有任何疑问，您可通过本政策"如何联系我们"章节所列的反馈渠道联系我们，我们会尽快为您做出解答。
</p>
<p>1、帮助您完成注册及登录</p>
<p>为了体验更丰富完善的本平台服务，您可以注册并登录本平台账号。您需要提供基本注册或登录信息，包括手机号码、验证码匹配结果（敏感个人信息），并创建您的账号、用户名和密码。</p>
<p>您也可以使用我们支持的第三方平台账号进行登录。如您使用第三方平台的账号登录，我们将根据您的授权获取该第三方账号下的相关信息（包括：用户名、昵称、头像、地区、性别，具体以您的授权内容为准）以及身份验证信息（手机号码、验证码匹配结果）。我们收集这些信息是用于为您提供账号登录服务以及保障您的账号安全。如您拒绝授权此类信息
    ，您将无法使用第三方平台的账号登录我们平台，但不影响我们为您提供的其他功能的正常使用。</p>
<p>在注册过程中，如果您提供以下额外信息，将有助于我们给您提供个性化的推荐和更优的服务体验：例如您的呢称、简介、头像、性别、所在地区。如果您不提供这些信息，
    并不会影响您使用本平台基本功能。同时，我们也提供注册账号的注销功能，在符合服务协议约定条件及国家相关法律法规规定的情况下，您可以自行在“删除账号”页面注销账号。您可以登录本平台，按照提示进行相应的操作。</p>
<p>在您的本平台账号注销之后，我们将停止为您提供本平台服务，并依据您的要求，删除您的个人信息或做匿名化处理，但法律法规另有规定的除外。</p>
<p>2、实现身份认证</p>
<p>依照相关法律法规的要求，或为满足用户身份真实核验、保障系统和服务安全需要，您在使用特定功能及服务前，可能需要进行实名认证。我们将收集您的身份认证信息。如：当您申请网上咨询、网上诊疗、购药、就医代理服务、保险服务等领域，我们需要收集您的手机号码、身份证号码、真实姓名、面部识别特征（静态或动态），并进行人脸识别认证并且可能需要搜集您的病例、诊断证明、X光照片等与诊疗相关的文件及资料；请注意，您的手机号码、验证码匹配结果、身份证号码、真实姓名、面部特征识别信息、诊疗信息等属于敏感个人信息，请您谨慎提供，如果拒绝提供您将可能无法获得相关服务，但不影响其他功能与服务的正常使用。
</p>
<p>3、维护基本功能的正常运行</p>
<p>在您使用我们服务过程中，为识别账号异常状态、了解产品适配性，向您提供浏览、搜索等基本服务，保障服务正常运行，优化服务体验，我们可能会直接或间接地收集、存储关于您使用的服务以及使用方式的信息并将这些信息进行关联，这些信息包括</p>
<p>（1）日志信息：当您使用我们的服务时，我们可能会自动收集（包括在后台运行状态下收集）您对我们服务的详细使用情况，作为有关网络日志保存。例如您的登录账号、搜索查询内容、IP地址、浏览器的类型、电信运营商、网络环境、使用的语言、访问日期和时间及您访问的网页浏览记录、Push打开记录、停留时长、刷新记录、发布记录、关注、订阅、收藏及分享。
