<p>（2）设备信息：我们可能会根据您在软件安装及使用中授予的具体权限，接收并记录（包括在后台运行状态下接收和记录）您所使用的设备相关信息，例如设备机型、操作系统及版本、客户端版本、设备分辨率、包名、设备设置、设备标识符（MAC地址/IMEI/Android
    ID/Open UDID/GUID/MAC/
    OAID/IDFA/SIM卡IMSI信息）、软硬件特征信息）、设备所在位置相关信息（例如IP地址、GPS位置信息）。为了收集上述个人设备信息，我们将会申请访问您设备的相关权限，包括设备信息权限、位置权限等。</p>
<p>请注意，单独的设备信息、日志信息是无法识别特定自然人身份信息。如果我们将前述信息与其他个人信息结合用于识别特定自然人身份，或者将与能够识别自然人身份的信息结合使用，则在结合使用期间，除取得您授权或法律法规另有规定外，我们会将该类个人信息做匿名化、去标识化处理。这些信息在结合使用期问，我们将其按照本政策予以处理与保护。
</p>
<p>4、病例病案提供及诊断、医疗服务</p>
<p>在您使用过程中，为了保证诊疗或医疗代理服务的安全及准确，我们需要收集您的诊疗历史信息，包括但不限于搜集您的原有病例、诊断证明、X光照片等与诊疗相关的文件及保险单等保险文件；同时我们还将在诊疗期间通过视频联网等方式与您沟通，保留您网络诊断期间的视频、语音、图片、文字及病例病案、诊断证明等资料。请注意，您的上述诊疗历史信息、诊断期间的信息及保险信息等均属于敏感个人信息，请您谨慎提供，如果拒绝提供或拒绝摄像头的使用，您将可能无法获得相关服务，但不影响其他功能与服务的正常使用。
</p>
<p>5、提供搜索服务</p>
<p>当您使用搜索功能时，我们需要收集您搜索的字词、浏览记录和时间、搜索的时间以及与它们互动的次数。我们收集这些信息是为了向您提供您所需要的内容和可能更感兴趣的服务。</p>
<p>6、扫描二维码/AR扫一扫/拍摄照片及相关的编辑功能</p>
<p>当您使用扫描二维码/AR扫一扫/拍摄照片功能时，我们需要您授权我们访问您设备的相机权限、存储权限，并收集您提供的基于扫描二维码、拍摄照片后上传的图片信息。您的照片可能包含敏感个人信息，请您谨慎提供。</p>
<p>7、基于地理位置的功能及服务</p>
<p>如您使用基于地理位置来展示附近的内容与活动的功能或服务，我们需要您授权我们访问您的位置权限，并收集您的位置信息，以便向您推荐附近的服务、内容或活动。您的位置信息属于敏感个人信息，请您谨慎提供。</p>
<p>8、信息发布功能</p>
<p>当您使用本平台中的信息发布功能，根据相关法律法规的要求，我们需要收集您的手机号码及验证码匹配结果（敏感个人信息）。同时，我们可能需要根据您的发布行为和所发布内容的类型，收集相应的个人信息、获取相应的权限。如我们需要您授权我们访问您的相机权限及/或照片权限、存储权限，并收集您需要发布的照片或视频信息可能包含敏感个人信息）。如您发布图文或视频时需要同时发布您的位置，我们需要您授权我们访问您的位置权限，并收集您的位置信息（敏感个人信息），请您注意，如您发布的信息涉及到他人的个人信息的，您应当在发布前征得他人同意。
</p>
<p>9、商品购买及支付功能</p>
<p>为便于向您交付商品或服务，您至少需按照提示向我们提供收货人个人身份信息、姓名、收货地址、邮政编码、收货人、联系电话、支付状态信息。如果您拒绝提供此类信息，我们将无法完成相关交付服务。如您通过本平台为其他人订购商品或服务，您需要提供该实际订购人的前述信息。向我们提供该实际订购人的前述信息之前，您需确保您已经取得其授权同意。
</p>
<p>如您在我们的服务过程中产生支付结算，支付结算过程中，您可以选择我们的关联方或与我们合作的第三方支付机构（包括支付宝支付、微信支付、Apple
    Pay及银联、网联等支付通道，以下称"支付机构"）所提供的支付服务。支付服务过程中我们可能需要将您的本平台订单号与交易金额信息同这些支付机构共享以实现其确认您的支付指令并完成支付。</p>
<p>为展示您账户的订单信息及保障您的售后权益，我们会收集您在使用本平台过程中产生的订单信息、交易和消费记录、虚拟财产信息（敏感个人信息）用于向您展示及便于您对订单进行管理。如发生交易退费情形的，我们可能会要求您提供您的银行账号信息，包括银行卡信息、支付宝、微信支付第三方账号信息（敏感个人信息）用于为您处理退费请求。
</p>
<p>当您与我们联系时，我们可能会保存您的通信/通话记录和内容或您留下的联系方式及相关信息（敏感个人信息），以便与您联系或帮助您解决问题，或记录相关问题的处理方案及结果。</p>
<p>为确认交易状态及为您提供售后与争议解决服务，我们会通过您基于交易所选择的交易对象、支付机构、物流公司等收集与交易进度相关的您的交易、支付、物流信息，并将您的上述交易、支付、物流信息共享给相关的上述服务提供者。</p>
<p>10、提供商品或服务的信息展示和推送</p>
<p>基于您向我们提供的信息、我们可能收集的信息及我们通过间接方式收集到的您的信息，例如您的浏览、搜索记录、设备信息、位置信息、订单信息，我们可能会基于上述一项或几项信息的结合，进行推荐算法建模、程序化广告推荐算法建模、用户行为分析及用户画像，用于提取您的浏览、搜索偏好、行为习惯、位置信息相关特征，以便向您提供更契合您需求的页面展示和推荐个性化内容。
</p>
<p>我们也可能基于特征标签，通过上述“消息通知”中的方式，提供或推广我们或第三方的如下商品和服务；</p>
<p>我们的商品和服务，包括但不限于：网上媒体服务、支付服务、互联网搜索服务、位置和地图服务、应用软件和服务、数据管理软件和服务、网上广告服务、娱乐、网络游戏、电子商务、资讯及通讯软件和服务；及第三方商品和服务，包括但不限于：互联网服务、食物和餐饮、体育、音乐、电影、电视、现场表演和其他艺术和娱乐、书册、杂志和其他刊物、服装和配饰、珠宝、化妆品、个人健康和卫生、电子、收藏品、家用器皿、电器、家居装饰和摆设、宠物、汽车、酒店、交通和旅游、银行、保险及金融服务。我们会按照如下规则进行个性化推荐或定向推送，并为您提供相应的退出机制。
</p>
<p>如您需要关闭您为使用上述某些特定功能而开启的功能权限（包括相机权限、照片权限、麦克风权限、通讯录权限、存储权限、设备信息权限、地理位置权限、日历权限等），大多数移动设备都会支持您的这项需求，具体方法请参考或联系您移动设备的服务商或生产商。请您注意，您开启任一权限即代表您授权我们可以收集和使用相关信息来为您提供对应服务，您一旦关闭任一权限即代表您取消了授权，我们将不再基于对应权限继续收集和使用相关信息，也无法为您提供该权限所对应的服务。但是，您关闭权限的决定不会影响此前基于您的授权所进行的信息收集及使用。
</p>
<p>11、有关敏感个人信息的提示</p>
<p>敏感个人信息，是指一旦泄露或非法使用，容易导致自然人的人格尊严受到侵害或人身财产安全受到危害的个人信息。以上由您提供或我们收集您的信息中，可能包含您的敏感个人信息，例如身份证件号码、个人生物识别信息（包括面部识别特征、声纹等）、诊疗记录、医疗信息、银行账号、通讯录和好友列表、通信记录和内容、财产信息、行踪轨迹、位置信息、网页浏览记录、交易信息。请您谨慎并留意敏感个人信息。
</p>
<p></p>
<h4>二、我们如何使用COOKIES或同类技术</h4>
<p>我们或我们的合作伙伴可能通过COOKIES或同类技术获取和使用您的信息，并将该等信息存储为日志信息。</p>
<p>通过使用COOKIES，我们向用户提供简单易行并富个性化的网络体验。一个COOKIES是少量的数据，它们从一个网络服务器送至您的浏览器并存在计算机硬盘上。我们使用COOKIES是为了让其用户可以受益。比如，为使得本平台的登录过程更快捷，您可以选择把用户名存在一个COOKIES中。这样下次当您登陆本平台的服务时能更加方便。
</p>
<p>COOKIES使得我们能更好、更快地为您服务。然而，您应该能够控制COOKIES是否以及怎样被您的浏览器接受。请查阅您的浏览器附带的文件以获得更多这方面的信息。</p>
<p>我们和第三方合作伙伴可能通过COOKIES或同类技术收集和使用您的信息，并将该等信息存储。我们使用自己的COOKIES或同类技术，可能用于以下用途∶</p>
<p>（1）记住您的身份。例如：COOKIES或同类技术有助于我们辨认您作为我们的注册用户的身份，或保存您向我们提供有关您的喜好或其他信息；</p>
<p>（2）分析您使用我们服务的情况。我们可利用COOKIES或同类技术来了解您使用本平台进行什么活动、或哪些服务最受欢迎；</p>
<p>（3）广告优化。COOKIES或同类技术有助于我们根据您的信息，向您提供与您相关的广告而非进行普遍的广告投放。</p>
<p>我们为上述目的使用COOKIES或同类技术的同时，可能将通过COOKIES或同类技术收集的非个人身份信息汇总提供给广告商和其他伙伴，用于分析您和其他用户如何使用本平台并用于广告服务。</p>
<p>本平台上可能会有广告商和其他合作方放置的COOKIES或同类技术。这些COOKIES或同类技术可能会收集与您相关的非个人身份信息，以用于分析用户如何使用该等服务、向您发送您可能感兴趣的广告，或用于评估广告服务的效果。</p>
<p>您可以通过浏览器或用户选择机制拒绝或管理COOKIES。但请您注意，如果您停用COOKIES，我们有可能无法为您提供最佳的服务，其些服务也可能无法正常使用。同时，您仍然将收到广告，只是这些广告与您的相关性会降低。</p>
<h4>三、我们可能共享、转让或披露的个人信息</h4>
<h4>（一）共享</h4>
<p>我们非常重视保护您的个人信息。除以下情形外，未经您另行同意，我们不会与我们之外的任何第三方共享您的信息：</p>
<p>1、为实现我们的服务/功能所必需进行的共享。在某些情况下，我们需要向第三方共享您的信息，以实现我们产品与/或服务的功能，让您正常使用您需要的服务，包括：（1）向提供支付服务的第三方支付机构共享对应的交易订单信息和支付金额信息；（2）向实名认证服务商共享实名认证所需资料；（3）向与我们合作的医疗机构、保险公司提供您的个人信息、诊疗记录及相关信息；（4）向医疗提供者、服务商共享订单信息；（5）其他与我们的服务/功能相关的供应商、服务提供商和业务合作伙伴所必需进行的共享。
</p>
<p>2、与我们的部分合作伙伴的必要共享。为了必要/合理的业务的顺利开展、满足您的要求、履行我们在相关用户协议或本政策中的义务和行使我们的权利或遵守法律规定等目的，我们可能需要向部分合作伙伴共享您的信息，包括：（1）向提供数据服务（包括网络广告监测、数据统计、数据分析等）的合作伙伴共享日志信息、设备信息；（2）向提供产品分析服务（包括应用崩溃分析、网络性能分析、域名解析优化）的合作伙伴共享您的产品使用情况（如崩溃、闪退）的统计性数据；（3）向提供营销服务（包括广告投放、市场营销）的合作伙伴共享您的相关信息（包括：设备信息、IP地址）（4）向委托我们进行推广的合作伙伴共享有多少人看了他们的推广信息或在看到这些信息后购买的商品，或者向他们提供不能识别个人身份的统计信息，帮助他们了解其受众或顾客；（5）其他与我们的部分合作伙伴的必要共享。
</p>
<p>3、与关联方的必要共享。为便于我们基于统一的帐号体系向您提供一致化服务以及便于您进行统一管理、保障系统和帐号安全等，您的个人信息可能会在我们和我们的关联方之间进行必要共享，包括：</p>
<p>（1）为了帮助您节省时间、简化操作流程，您在注册我们的帐号后无需重复注册即可登录部分单项服务，您的帐号相关信息会在我们和我们的关联方的产品内部进行必要共享；</p>
<p>（2）为了使您在我们的及我们关联方产品与/或服务有一致性和个性化的浏览、广告等服务体验，您知悉并同意，您的帐号信息、设备信息、相关浏览数据等反映您使用习惯和爱好的信息可能会与我们的关联方共享。</p>
<p>4、实现本政策第一条“我们如何收集和使用个人信息”部分所述的其他目的。</p>
<p>5、履行我们在本政策或我们与您达成的其他协议中的义务和行使我们的权利。</p>
<p>6、在法律法规允许的范围内，为了道守法律、维护我们及我们的关联方或合作伙伴、您或其他用户或社会公众利益、财产或安全免遭损害，比如为防止欺诈等违法活动和减少信用风险，我们可能与其他公司和组织交换信息。不过，这并不包括违反本政策中所作的承诺而为获利目的出售、出租、共享或以其它方式披露的信息。
</p>
<p>7、应您合法需求或经您的授权同意。</p>
<p>8、应您的监护人合法要求而提供您的信息。</p>
<p>9、根据与您签署的单项服务协议（包括在线签署的电子协议以及相应的平台规则）或其他的法律文件约定所提供。</p>
<p>10、基于学术研究而提供。</p>
<p>11、基于符合法律法规的社会公共利益而提供。</p>
<p>我们仅会以合法、正当、必要、特定、明确的目的共享您的个人信息。对我们与之共享个人信息的公司、组织和个人，我们会与其签署严格的保密协定，要求他们按照我们的说明、本政策以及其他任何相关的保密和安全措施来处理信息。</p>
<h4>（二）转让</h4>
<p>1、随着我们业务的持续发展，我们有可能进行合并、收购、资产转让或类似的交易，而您的信息有可能作为此类交易的一部分而被转移。我们会要求新的持有您信息的公司、组织继续受本政策的约束，否则，我们将要求该主体重新向您征求授权同意。</p>
<p>2、在获得您的明确同意后，我们会向第三方转让您的信息。</p>
<h4>（三）披露</h4>
<p>我们仅会在以下情况下，且采取符合业界标准的安全防护措施的前提下，才会披露您的信息：</p>
<p>1、根据您的需求，在您明确同意的披露方式下披露您所指定的信息;</p>
<p>2、根据法律、法规的要求、强制性的行政执法或司法要求所必须提供您信息的情况下，我们可能会依据所要求的信息类型和披露方式披露您的信息。在符合法律法规的前提下，当我们收到上述披露信息的请求时，我们会要求接收方必须出具与之相应的法律文件，如传票或调查函。我们坚信，对于要求我们提供的信息，应该在法律允许的范围内尽可能保持透明。我们对所有的请求都进行了慎重地审查，以确保其具备合法依据，且仅限于执法部门因特定调查目的且有合法权利获取的数据。
</p>
<h4>（四）共享、转让、披露信息时事先征得授权同意的例外</h4>
<p>以下情形中，共享、转让、披露您的信息无需事先征得您的授权同意：</p>
<p>1、与国家安全、国防安全直接相关的；</p>
<p>2、与公共安全、公共卫生、重大公共利益直接相关的；</p>
<p>3、与刑事侦查、起诉、审判和判决执行等直接相关的；</p>
<p>4、出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</p>
<p>5、您自行向社会公众公开的信息；</p>
<p>6、从合法公开披露的信息中收集信息的，如合法的新闻报道、政府信息公开等渠道；</p>
<p>7、与我们履行法律法规规定的义务相关的。</p>
<p>根据法律规定，共享、转让经匿名化处理的信息，且确保数据接收方无法复原并重新识别个人信息主体的，不属于信息的对外共享、转让及公开披露的行为，对此类数据的保存及处理将无需另行向您通知并征得您的同意。</p>
<h4>四、我们如何存储和保护个人信息</h4>
<p>（一）个人信息的存储</p>
<p>除法律法规或监管部门另有规定外，我们仅在本政策所述目的所必需且最短时限内存储您的信息。如我们终止服务或运营，我们将及时停止继续收集并在终止服务或运营后对您的个人信息进行删除或匿名化处理，同时会遵守相关法律法规之规定。</p>
<p>我们在中华人民共和国境内运营中收集和产生的信息，存储在中国境内，以下情形除外：</p>
<p>1、法律法规有明确规定；</p>
<p>2、获得您的授权同意；</p>
<p>3、您使用的产品、服务涉及跨境，我们需要向境外提供您的信息的。</p>
<p>针对以上情形，我们会确保依据国家法律法规和相关监管部门的规定执行，并对您的信息提供足够的保护。</p>
<p>（二）个人信息的保护</p>
<p>我们非常重视个人信息安全，成立了专门的安全团队，并采取一切合理可行的措施，保护您的信息。</p>
<p>1、数据安全技术措施</p>
<p>我们会采用符合业界标准的安全防护措施，包括建立合理的制度规范、安全技术来防止您的信息遭到未经授权的访问使用、修改避免数据的损坏或丢失。网络服务采取了多种加密技术，例如在某些服务中，我们将利用加密技术（例如SSL）来保护您的信息，采取加密技术对您的信息进行加密保存，并通过隔离技术进行隔离。在信息使用时，例如信息展示、信息关联计算，我们会采用多种数据脱敏技术增强信息在使用中的安全性。采用严格的数据访问权限控制和多重身份认证技术保护信息，避免数据被违规使用。
</p>
<p>2、 我们为保护信息采取的其他安全措施</p>
<p>我们通过建立数据分类分级制度、数据安全管理规范、数据安全开发规范来管理规范信息的存储和使用。我们通过信息接触者保密协议、监控和审计机制来对数据进行全面安全控制。</p>
<p>加强安全意识。我们还会举办安全和隐私保护培训课程，加强员工对于个人信息保护政策和相关规程的认识。</p>
<p>3、我们仅允许有必要知晓这些信息的公司员工、合作伙伴访问您的信息，并为此设置了严格的访问权限控制和监控机制。我们同时要求可能接触到您的信息的所有人员履行相应的保密义务。如果未能履行这些义务，可能会被追究法律责任或被终止与本平台的合作关系。
</p>
<p>4、互联网并非绝对安全的环境，而且电子邮件、即时通讯、社交软件或其他服务软件等与其他用户的交流方式无法确定是否完全加密，我们建议您使用此类工具时请使用复杂密码，并注意保护您的信息安全。</p>
<p>5、互联网环境并非百分之百安全，我们将尽力确保或担保您发送给我们的任何信息的安全性。如果我们的物理、技术、或管理防护设施遭到破坏，导致信息被非授权访问、公开披露、篡改、或毁坏，导致您的合法权益受损，我们将依照法律规定承担相应责任。
</p>
<p>6、安全事件处置</p>
<p>在通过本平台与第三方进行沟通或购买商品及接受服务时，您不可避免的要向交易对方或潜在的交易对方披露自己的信息，如联络方式或者邮政地址等。请您妥善保护自己的信息，仅在必要的情形下向他人提供。</p>
<p>为应对个人信息泄露、损毁和丢失等可能出现的风险，我们制定了多项制度，明确安全事件、安全漏洞的分类分级标准及相应的处理流程。我们也为安全事件建立了专门的应急响应团队，按照安全事件处置规范要求，针对不同安全事件启动安全预案，进行止损、分析、定位、制定补救措施、联合相关部门进行溯源和打击。我们亦定期组织内部培训和应急演练，使得我们的相关工作人员掌握相应的应急处置策略和规程。
</p>
<p>在不幸发生信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们同时将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，主动上报信息安全事件的处置情况。
</p>
<p>请您理解，由于技术的限制以及风险防范的局限，即便我们已经尽量加强安全措施，也无法始终保证信息百分之百的安全。您需要了解，您接入本平台所用的系统和通讯网络，有可能因我们可控范围外的情况而发生问题。</p>
<p>请您务必妥善保管好您的帐号、密码及其他身份要素。您在使用本平台服务时，我们会通过您的帐号、密码及其他身份要素来识别您的身份。一旦您泄露了前述信息，您可能会蒙受损失，并可能对您产生不利。如您发现帐号、密码及/或其他身份要素可能或已经泄露时，请您立即和我们取得联系，以便我们及时采取相应措施以避免或降低相关损失。
</p>
<p>7、我们有行业先进的以数据为核心，围绕数据生命周期进行的数据安全管理体系，从组织建设、制度设计、人员管理、产品技术等方面多维度提升整个系统的安全性。</p>
<h4>五、如何管理个人信息</h4>
<p>（一）访问、更新和删除</p>
<p>我们鼓励您更新和修改您的信息以使其更准确有效。您可以通过本平台公示的方式访问、更新和删除您的个人信息。</p>
<p>您还可以通过本政策“如何联系我们”章节所列的反馈渠道联系我们为您处理。我们将采取适当的技术手段或联系渠道，尽可能保证您可以访问、更新和删除自己的个人信息或使用本平台服务时提供的其他信息。</p>
<p>除法律法规另有规定，当您更正、删除您的个人信息时，我们可能不会立即从备份系统中更正或删除相应的信息，但会在备份更新时更正或删除这些信息。</p>
<p>（二）帐号注销</p>
<p>我们为您提供注册帐号的注销功能，在符合服务协议约定条件及国家相关法律法规规定的情况下，您可以自行在本平台“注销账号”页面注销账号。您可以登录本平台官网，按照提示进行相应的操作。您也可以选择通过本政策“如何联系我们”章节所列的反馈渠道联系我们，我们会尽快为您作出解答。
</p>
<p>在您的本平台账号注销之后，我们将停止为您提供本平台服务，并依据您的要求，删除您的个人信息或做匿名化处理，但法律法规另有规定的除外。</p>
<p>（三）公开与分享</p>
<p>我们的多项服务可让您不仅与您的社交网络、也与使用该服务的所有用户公开分享您的相关信息，例，您在本平台中所上传或发布的信息、您对其他人上传或发布的信息作出的回应，在本平台中不特定用户可见的公开区域内上传或公布您的信息，以及包括与这些信息有关的位置数据和日志信息。只要您不删除您所公开或共享的信息，有关信息可能一直留存在公众领域；即使您删除共享信息，有关信息仍可能由其他用户或不受我们控制的第三方独立地缓存、复制或存储，或由其他用户或该等第三方在公众领域保存。如您将信息通过上述渠道公开或共享，由此造成您的信息泄露，我们不承担责任。因此，我们提醒并请您慎重考虑是否通过上述渠道公开或共享您的信息。
</p>
<p>（四）改变您授权同意的范围</p>
<p>您总是可以选择是否披露信息。有些信息是使用本平台所必需的，但大多数其他信息的提供是由您决定的。您可以通过删除信息、关闭设备功能等方式改变您授权我们继续收集信息的范围或撤回您的授权。</p>
<p>当您撤回授权后，我们无法继续为您提供撤回授权所对应的服务，也不再处理您相应的信息。但您撤回授权的决定，不会影响此前基于您的授权而开展的信息处理。</p>
<p>（五）个人信息副本获取</p>
<p>如您需要您的个人信息的副本，您可以通过本政策“如何联系我们”章节所列的反馈渠道联系我们，在核实您的身份后，我们将向您提供您在我们的服务中的个人信息副本（包括基本资料、身份信息），但法律法规另有规定的或本政策另有约定的除外。</p>
<p>（六）响应您的上述请求</p>
<p>为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情收取一定成本费用。对于那些无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际（例如，涉及备份磁带上存放的信息）的请求，我们可能会予以拒绝。
</p>
<p>在以下情形中，我们将无法响应您的请求：</p>
<p>1、与我们履行法律法规规定的义务相关的；</p>
<p>2、与国家安全、国防安全直接相关的；</p>
<p>3、与公共安全、公共卫生、重大公共利益直接相关的；</p>
<p>4、与刑事侦查、起诉、审判和执行判决等直接相关的；</p>
<p>5、我们有充分证据表明您存在主观恶意或滥用权利的；</p>
<p>6、出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</p>
<p>7、响应您的请求将导致您或其他个人、组织的合法权益受到严重损害的；</p>
<p>8、涉及商业秘密的。</p>
<h4>六、第三方服务</h4>
<p>本平台可能接入或链接至第三方提供的社交媒体或其他服务（包括网站或其他服务形式）。包括：</p>
<p>（1）您可利用“分享”键将本平台中的某些内容分享到第三方。或者，您可利用第三方服务登录本平台。这些功能可能会收集您的信息（包括您的日志信息），并可能在您的设备内装置COOKIES，从而正常运行上述功能。</p>
<p>（2）我们通过广告或我们服务的其他方式向您提供链接，使您可以接入第三方的服务或网站，或其他接入第三方服务的情形。例，为实现本政策中声明的目的，我们可能会接入第三方SDK服务或其他类似的应用程序（如以嵌入代码、插件等形式），以便提高更好的客户服务和用户体验。目前，我们接入的第三方服务商主要包括以下几种类型：
</p>
<p>1、用于医疗服务，包括合作医疗机构、医生、保险公司等与医疗服务及保险服务的；</p>
<p>2、用于广告相关服务，包括广告展示、广告统计及监控等；</p>
<p>3、用于消息推送功能，包括手机厂商Push推送、特定事件提醒等；</p>
<p>4、用于支付相关服务，包括订单支付、交易行为核验、收入结算、支付信息汇总统计等；</p>
<p>5、用于在您同意的情况下获取设备位置权限、搜集设备信息和日志信息等；</p>
<p>6、用于第三方授权服务，包括第三方账号登陆、将有关内容分享至第三方产品等；</p>
<p>7、用于支持产品功能模块，包括视频播放、语音识别、AR、智能客服、内容存储等；</p>
<p>8、用于优化产品性能，包括提高硬件配网能力、降低服务器成本、功能热修复等；</p>
<p>9、用于账号安全、产品加固相关服务，包括网络监测、域名解析、防劫持、反垃圾反作弊、加解密服务等。</p>
<p>我们接入的部分第三方SDK可能涉及收集用户信息，用于向用户提供相应的服务。如您在我们的服务中使用这类由第三方提供的服务时，您同意将由其直接收集和处理您的信息。我们会评估该第三方收集信息的合法性、正当性、必要性，要求第三方对您的信息采取保护措施，并严格遵守相关法律法规与监管要求。
</p>
<p>第三方社交媒体，第三方支付、第三方游戏或其他第三方服务，由相关的第三方负责运营。您使用该等第三方服务（包括您向该等第三方提供的任何信息），须受第三方自己的服务条款及信息保护声明（而非本政策）约束，您需要仔细阅读其条款。本政策仅适用于我们所收集的个人信息，并不适用于任何第三方提供的服务或第三方的信息使用规则。如您发现这些第三方服务存在风险时，建议您终止相关操作以保护您的合法权益并及时与我们取得联系。
</p>
<h4>七、未成年人保护</h4>
<p>我们建议：任何未成年人参加网上活动应事先取得父母或其他监护人（简称监护人）的同意，我们将根据国家相关法律法规的规定保护未成年人的相关信息。</p>
<p>我们鼓励监护人指导未成年人使用本平台服务。如果您是未满十四周岁的未成年人，请您通知您的监护人共同阅读并接受我们的《儿童用户信息保护规则及监护人须知》，并在您使用本平台服务、提交个人信息之前，寻求其同意和指导。</p>
<h4>八、修订和通知</h4>
<p>为了给您提供更好的服务，我们可能会根据本平台服务的更新情况及法律法规的相关要求适时修改本政策的条款，并在本页面发布对本政策的任何修改，该等修改构成本政策的一部分。对于重大变更，我们还会提供更为显著的通知（包括网站公告、推送通知、弹窗提示或其它方式）。本政策所指的重大变更包括但不限于：
</p>
<p>1、我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；</p>
<p>2、我们在所有权结构方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；</p>
<p>3、个人信息共享、转让或公开披露的主要对象发生重大变化；</p>
<p>4、您参与个人信息处理方面的权利及其行使方式发生重大变化；</p>
<p>5、我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生重大变化；</p>
<p>6、其他重要的或可能严重影响您的个人权益的情况发生。</p>
<p>我们的任何修改都会将您的满意度置于首位。我们鼓励您在每次使用本平台服务时都查阅我们的《用户信息隐私政策》。</p>
<h4>九、如何联系我们</h4>
<p>本平台将按照本政策保护您的个人信息。我们设立了个人信息保护专职部门。如您有关于网络信息安全的投诉和举报，或您对本政策、您的信息的相关事宜有任何问题、意见或建议，以及有关本政策或本平台的隐私措施的问题，请随时与我们联系，您可通过在线客服入口联系我们。
</p>
<p>一般情况下，我们将在收到您的问题、意见或建议，并验证您的用户身份后十五个工作日内予以处理。如您不满意我们的处理，您还可以向履行个人信息保护职责的部门进行投诉、举报。</p>
