  onShow() {},
  onLoad(e) {
    console.log(e);
    if (e?.mode) {
      this.mode = e.mode;
    }
  },
  methods: {
    onTapFile(index) {
      if (index === 1) {
        this.content = this.html1;
      } else if (index === 2) {
        this.content = this.html2;
      } else if (index === 3) {
        this.content = this.html3;
      }
      this.show = true;
    },
    // 点击actionSheet回调
    actionSheetCallback(index) {
      uni.hideKeyboard();
      this.form.credentialTypeName = this.actionSheetList[index].text;
      this.form.credentialType = this.actionSheetList[index].id;
    },
    checkboxChange(e) {
      this.disabled = !e.value;
    },
    codeChange(text) {
      this.codeTips = text;
    },
    sendSms() {
      // console.log(this.$refs.uCode,'显示数据哈哈哈')
      // console.log("1234");
      if (this.canGetCode) {
        uni.showLoading({
          title: "正在获取验证码",
        });
        sendLoginVCode({ mobile: this.form.mobile }).then((res) => {
          if (res.code == "0") {
            this.$u.toast("发送成功");
            this.isDisabled = true;
            let timerId = setInterval(() => {
              this.CountDown--;
              this.codeTips = this.CountDown + "秒后重试";
              if (this.CountDown <= 0) {
                clearInterval(timerId);
                this.codeTips = "重新发送";
                this.CountDown = 60;
                this.isDisabled = false;
              }
            }, 1000);
          }
        });
      } else {
        this.$u.toast("倒计时结束后再发送");
      }
    },
    login() {
      if (this.mode == "open"  || this.mode == "") {
        console.log('ronin:45678')
        thirdBindByMobileYesReg(this.form).then((res) => {
          if (res.code == "0" && res.data) {
            this.$u.vuex("vuex_user", res.data);
            if (res.data.token) {
              this.$u.vuex("vuex_token", res.data.token);
            }
            uni.switchTab({ url: "/pages/home/home" });
            uni.navigateTo({ url: "/pages/home/home" });
          } else if (res.code === "1022") {
            uni.showModal({
              title: "认证失败",
              content: "请使用承保人身份信息登录",
              showCancel: false,
            });
          }
        });
